import backgroundImg from '../../images/blackAndWhite.jpg';
import React, { useContext, useEffect, useState, useCallback } from 'react';
import { ApiContext } from '../../api';
import styles from './movieContainer.module.css';

//for animation
import { TweenMax, TimelineLite, Power0, Power2 } from 'gsap';

const MoviesContainer = () => {
    //getting data
    const { data, loading, movieData, getMovieData, setMovieData } = useContext(ApiContext);

    //state to close movie details card
    let [closed, setClosed] = useState(true);

    //get data of clicked movie
    const movieClicked = (movieId) => {
        if (window.innerWidth < 768) {
            document.getElementById('container').style.display = 'none';
        }
        getMovieData(movieId);
    }

    //function to manipulate 'closed' state
    const close = useCallback(() => {
        let movieDetailsWrapper = document.getElementById('movieDetailsWrapper');
        let closeBtn = document.getElementById('closeBtn');

        if (movieDetailsWrapper !== null || closeBtn !== null) {
            closeBtn.addEventListener('click', () => {
                if (window.innerWidth < 768) {
                    document.getElementById('container').removeAttribute('style');
                }
                setClosed(true);
                setMovieData(undefined);
            })
        }

    }, [setMovieData]);

    //animation after 'data' changes
    useEffect(() => {
        const tl = new TimelineLite();
        let movies = document.querySelectorAll('#movie');

        if (movies !== undefined || movies !== null) {
            tl.from(movies, 1.5, { y: -50, opacity: 0, stagger: { each: 0.1, ease: Power0.easeNone } }, "+=0.2");
        }
    }, [data])

    //animation and fix close movie card bug
    useEffect(() => {
        let bg = document.getElementById('bg');

        //animation of background image
        if (bg !== undefined || bg !== null) {
            TweenMax.to(bg, 15, { scale: 1.5, transformOrigin: "50% 50%", repeat: -1, yoyo: true, ease: Power2.easeInOut });
        }

        //change state of closed
        movieData !== undefined ? (setClosed(false)) : (setClosed(true));

        //Conditional operator aka Ternary operator didn't work so I should use if statement
        if (!closed) {
            close();
        }

    }, [movieData, close, closed])

    //runs when data is fetched and loading is not true
    if (data !== undefined && !loading) {
        return (
            <React.Fragment>
                <div className={styles.backgroundImgHolder} id='bg'>
                    <div className={styles.blackOverlay}></div>
                    <img src={backgroundImg} className={styles.bgImg} alt='backgroundImg' />
                </div>
                <div className={styles.container} id='container'>
                    {/* mapping the data array to display movies */}
                    {
                        data.map((data) => {
                            return (
                                <div className={styles.movieWrapper} id='movie' onClick={() => movieClicked(data.imdbID)} key={data.imdbID} >
                                    <div className={styles.movieContainer}>

                                        {/* using conditional operator to see if image aka poster is available or not */}
                                        {data.Poster !== 'N/A' ? (
                                            <div className={styles.detailsContainer}>
                                                <img src={data.Poster} className={styles.poster} alt={data.Title} data-id={data.imdbID} />
                                            </div>
                                        ) : (
                                                <div className={styles.detailsContainer}>
                                                    <h3 className={styles.imgNotAvailable}>Image not Available!<br /> :(</h3>
                                                    <img className={styles.poster} alt='' data-id={data.imdbID} />
                                                </div>
                                            )}

                                        <p className={styles.title}>{data.Title}</p>
                                        <p className={styles.year}>{data.Year}</p>
                                    </div>
                                </div>
                            );
                        })
                    }
                </div >

                {/* movie details displayed when a movie is clicked */}
                {
                    (movieData !== undefined && !closed) ? (
                        <div className={styles.movieDetailsWrapper} id='movieDetailsWrapper'>
                            <div className={styles.movieDetailsContainer}>
                                <div className={styles.movieDetails}>
                                    <img src={movieData.Poster} alt={movieData.Title} />
                                    <div>
                                        <h1>{movieData.Title} ({movieData.Year})</h1>
                                        <h3>Ratings: {movieData.Ratings[0].Value}</h3>
                                        <p className={styles.fonts}>Released Data: {movieData.Released}</p>
                                        <p>{movieData.Plot}</p>
                                        <div className={styles.flex}>
                                            <p className={styles.fonts}>Director/s:</p>
                                            <p className={styles.marginLeft}>{movieData.Director}</p>
                                        </div>
                                        <p className={styles.fonts}>Writer/s:</p>
                                        <p>{movieData.Writer}</p>
                                        <input type='button' id='closeBtn' onClick={close} value='Close' />
                                    </div>
                                </div>
                            </div>
                        </div>
                    ) : ('')
                }
            </React.Fragment>
        )
    }

    // displayed when there is no movies when searched
    else {
        return (
            <React.Fragment>
                <div className={styles.backgroundImgHolder} id='bg'></div>
                <div className={styles.noData}>
                    <h1>No Movies Found! :(</h1>
                </div>
            </React.Fragment>
        )
    }

}

export default MoviesContainer;