import React, { createContext, useState, useEffect } from 'react';

export const ApiContext = createContext();

const ApiContextProvider = (props) => {

    const apiLink = 'http://www.omdbapi.com/';
    const apiKey = 1200995;

    //state for storing movies list
    const [data, setData] = useState();

    //waiting for data
    const [loading, setLoading] = useState(true);

    //storing clicked movie data
    const [movieData, setMovieData] = useState();

    //state for searching the movie
    const [search, setSearch] = useState('Avengers');

    //set search to 'avengers' if search is null
    if (search === '') {
        setSearch('Avengers');
    }

    //create api url from search
    let searchString = `${apiLink}?apikey=${apiKey}&s=${search}&page=1`;
    let searchString2 = `${apiLink}?apikey=${apiKey}&s=${search}&page=2`;

    //get the movies
    const getData = async (searchString) => {
        const res = await fetch(searchString);

        const data = (await res.json()).Search;

        const res2 = await fetch(searchString2);

        const data2 = (await res2.json()).Search;

        if (data2 !== undefined) {
            return data.concat(data2);
        }
        return data;
    }

    //fetch api data and store it in data useState
    useEffect(() => {

        getData(searchString)
            .then(data => {
                setData(data);
                setLoading(false);
            })

    }, [searchString]);

    //change value of search
    const changeSearch = (search) => {
        setSearch(search);
    }
    const getMovieData = async (movieId) => {

        const fetchMovie = `${apiLink}?apikey=${apiKey}&i=${movieId}`;
        await fetch(fetchMovie)
            .then(response => response.json())
            .then(data => setMovieData(data))
    }

    if (!loading) {
        return (
            <ApiContext.Provider value={{ loading, search, data, movieData, changeSearch, getMovieData, setMovieData }}>
                {props.children}
            </ApiContext.Provider>
        )
    }

    //Loading section
    else {
        return (
            <h1 style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)' }}>
                Loading...
            </h1>
        )
    }


}

export default ApiContextProvider;